.main-nav {
  flex-grow: 1;
  display: flex;
  &__wrapper {
    display: flex;
    align-items: center;
    @include vp-1023 {
      display: none;
    }
  }
  &__list {
    display: flex;
    margin: 0;
    margin-right: 20px;
    margin-left: 60px;
    padding: 0;
    list-style: none;
  }
  &__item {
    &:not(:last-of-type) {
      margin-right: 20px;
    }
  }
  &__link {
    color: $color-bunting;
    font-weight: 500;
    font-size: 14px;
    line-height: 160%;
    text-transform: uppercase;
    &::after {
      background-color: $color-bunting;
    }
    &.is-active {
      opacity: 0.6;
    }
  }

  &__toggle {
    width: 36px;
    height: 36px;
    display: none;
    svg {
      width: 100%;
      height: 100%;
    }
    @include vp-1023 {
      display: block;
    }
  }
}
.line {
  fill: none;
  stroke: $color-navy-blue;
  stroke-width: 6;
  transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}
.line1 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}
.line2 {
  stroke-dasharray: 30 60;
  stroke-width: 6;
}
.line3 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}
.is-active .line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6;
}
.is-active .line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30;
  stroke-width: 6;
}
.is-active .line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6;
}
